import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {
  addContact,
  getContacts,
} from '../../../Redux/Contact/contact-operation';
import { getAllContacts } from '../../../Redux/Contact/contacts-selectors';

function ContactForm({ contacts, addContact, getContacts }) {
  const [state, setState] = useState({
    name: '',
    number: '',
  });

  useEffect(() => {
    getContacts();
  }, [getContacts]);

  const checkContact = () => {
    contacts.find(({ name }) => {
      return name === state.name;
    })
      ? alert(`${state.name} is already in contacts.`)
      : newContact();
  };

  const newContact = () => {
    const { name, number } = state;
    const newObj = { name, number };

    addContact(newObj);
  };

  const formInput = ({ target }) => {
    const { name, value } = target;
    setState({ ...state, [name]: value });
  };

  const formSubmit = e => {
    e.preventDefault();
    checkContact();
    reset();
  };

  const reset = () => {
    setState({ name: '', number: '' });
  };

  return (
    <>
      <h1>Phonebook</h1>
      <form onSubmit={formSubmit}>
        <label>
          Name
          <input
            type="text"
            name="name"
            value={state.name}
            placeholder="Kris Evans"
            pattern="^[a-zA-ZÐ°-ÑÐ-Ð¯]+(([' -][a-zA-ZÐ°-ÑÐ-Ð¯ ])?[a-zA-ZÐ°-ÑÐ-Ð¯]*)*$"
            title="ÐÐ¼Ñ Ð¼Ð¾Ð¶ÐµÑ ÑÐ¾ÑÑÐ¾ÑÑÑ ÑÐ¾Ð»ÑÐºÐ¾ Ð¸Ð· Ð±ÑÐºÐ², Ð°Ð¿Ð¾ÑÑÑÐ¾ÑÐ°, ÑÐ¸ÑÐµ Ð¸ Ð¿ÑÐ¾Ð±ÐµÐ»Ð¾Ð². ÐÐ°Ð¿ÑÐ¸Ð¼ÐµÑ Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan Ð¸ Ñ. Ð¿."
            required
            onChange={formInput}
          />
        </label>
        <label>
          Number
          <input
            type="tel"
            name="number"
            value={state.number}
            placeholder="555-55-55"
            pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
            title="ÐÐ¾Ð¼ÐµÑ ÑÐµÐ»ÐµÑÐ¾Ð½Ð° Ð´Ð¾Ð»Ð¶ÐµÐ½ ÑÐ¾ÑÑÐ¾ÑÑÑ ÑÐ¸ÑÑ Ð¸ Ð¼Ð¾Ð¶ÐµÑ ÑÐ¾Ð´ÐµÑÐ¶Ð°ÑÑ Ð¿ÑÐ¾Ð±ÐµÐ»Ñ, ÑÐ¸ÑÐµ, ÐºÑÑÐ³Ð»ÑÐµ ÑÐºÐ¾Ð±ÐºÐ¸ Ð¸ Ð¼Ð¾Ð¶ÐµÑ Ð½Ð°ÑÐ¸Ð½Ð°ÑÑÑÑ Ñ +"
            required
            onChange={formInput}
          />
        </label>
        <button className="submit_form" type="submit">
          Add contact
        </button>
      </form>
    </>
  );
}

const stateProps = state => ({
  contacts: getAllContacts(state),
});

const newContactDispatch = dispatch => ({
  addContact: contact => dispatch(addContact(contact)),
  getContacts: () => dispatch(getContacts()),
});

export default connect(stateProps, newContactDispatch)(ContactForm);

ContactForm.propTypes = {
  contacts: PropTypes.arrayOf(PropTypes.object),
};
